@font-face {
  font-family: 'StarWars';
  src: url('../font/starjedi.ttf');
}

body {
  font-family: 'Lato', sans-serif;
  margin: 0;
  background-color: #000000;
  background-image: url('../assets/background.png');
  background-position: center;
  color: #fbe000;
}

header {
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  font-family: 'StarWars';
  text-shadow: 0.2vh 0.2vh 0 #fbe000,
    0.2vh -0.2vh 0 #fbe000,
    -0.2vh 0.2vh 0 #fbe000,
    -0.2vh -0.2vh 0 #fbe000;
  color: #000000;
}

h1 {
  font-size: 3.8vh;
  line-height: 3vh;
  text-align: center;
}

main {
  height: 80vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

footer {
  min-height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.copyright {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.author {
  margin-left: 0.5vh;
}

.author:visited {
  color: #fbe000;
}

.author:hover {
  color: #fff384;
}



